  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: "Eund W00";
  src: url("../static/fonts/b33dcc4e-8871-4328-aea8-fa0ac5290746.woff2") format("woff2");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: "Eund W00";
  src: url("../static/fonts/68e00589-b50f-40ff-8423-13ec000df6b2.woff2") format("woff2");
  font-weight: 500;
  font-style: normal; }

@font-face {
  font-family: "Eund W00";
  src: url("../static/fonts/c3100e9c-1adb-4abb-9e9a-24006b893b1c.woff2") format("woff2");
  font-weight: 700;
  font-style: normal; }

@font-face {
  font-family: "Trade Gothic W01";
  src: url("../static/fonts/5414f75e-5cb8-4357-88e5-5a294424c859.woff2") format("woff2");
  font-weight: 200;
  font-style: normal; }

@font-face {
  font-family: "Trade Gothic W01";
  src: url("../static/fonts/1ba28851-f34b-4cb8-bf58-6a4b160ba249.woff2") format("woff2");
  font-weight: 700;
  font-style: normal; }

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0; }

body {
  font-family: 'Trade Gothic W01', sans-serif;
  font-weight: 200;
  color: #00415E;
  min-height: 100%; }

h3 {
  font-size: 25px;
  letter-spacing: 0.035em; }

p {
  font-size: 17px;
  line-height: 28px;
  margin-bottom: 24px; }

a {
  text-decoration: none;
  color: #00415E; }

ul, ol {
  list-style: none; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

/* Utilities */
.round-btn {
  padding: 10px 20px;
  border: 1px solid #00415E;
  border-radius: 5px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.1em; }
  .round-btn:hover {
    background-color: #00415E;
    color: #fff; }

/* Header */
.header {
  border-bottom: 2px solid #00415E;
  padding-top: 20px;
  padding-bottom: 20px; }

.header-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 30px;
  padding-right: 30px;
  font-size: 14px; }
  .header-container .address, .header-container .reservation {
    width: 240px;
    text-align: center;
    line-height: 1.5; }
  .header-container .reservation {
    text-align: right; }

.menu {
  width: 100%; }
  .menu-links {
    display: flex;
    justify-content: space-between;
    margin-left: 13%;
    margin-right: 13%; }
    .menu-links a {
      font-family: 'Eund W00';
      text-transform: uppercase;
      font-weight: 700;
      color: #00415e; }

/* Hero */
.hero {
  background: #FEDF63 url("https://thedarling.com/wp-content/themes/the-darling/img/bg-home.jpg") center top repeat-y;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 60px 0; }
  .hero-logo {
    display: block;
    width: 300px;
    height: auto;
    margin-bottom: 50px; }
  .hero-content {
    width: 100%;
    max-width: 740px;
    font-size: 28px;
    line-height: 46px;
    font-family: 'Eund W00', sans-serif;
    font-weight: 400;
    letter-spacing: 0.1rem;
    text-align: center;
    margin-bottom: 70px; }
  .hero-action {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 17px; }
    .hero-action img {
      width: 120px;
      margin-bottom: 25px; }
    .hero-action h3 {
      text-transform: uppercase;
      margin-bottom: 25px; }
    .hero-action p {
      font-family: 'Eund W00', sans-serif; }
    .hero-action a {
      text-decoration: underline; }

/* About */
.about {
  background: url("https://images.pexels.com/photos/6267/menu-restaurant-vintage-table.jpg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1520") no-repeat center center;
  background-size: cover;
  background-attachment: fixed;
  padding-top: 90px;
  padding-bottom: 90px;
  display: flex;
  justify-content: center; }
  .about-circle {
    background: url("https://thedarling.com/wp-content/themes/the-darling/img/circle.png") no-repeat center center;
    background-size: 100%;
    width: 100%;
    max-width: 459px;
    height: 459px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0 70px; }
    .about-circle h3 {
      margin-bottom: 20px;
      font-size: 25px; }
    .about-circle p {
      font-size: 17px;
      line-height: 28px;
      margin-bottom: 25px;
      text-align: center; }

/* Columns */
.more {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #fff; }

.two-columns {
  display: flex;
  width: 100%;
  max-width: 1200px; }
  .two-columns .left {
    background-color: #fff;
    display: flex;
    flex-direction: column;
    margin-bottom: 0px;
    width: 50%;
    padding: 10%;
    justify-content: center;
    align-items: center; }
    .two-columns .left h3, .two-columns .left p, .two-columns .left .round-btn {
      margin-bottom: 25px; }
    .two-columns .left .tagline {
      display: block;
      width: 100%;
      text-align: right;
      margin-bottom: 0px; }
  .two-columns .right {
    display: flex;
    width: 50%;
    background-color: #59906C;
    padding: 6%; }
    .two-columns .right img {
      width: 100%;
      height: auto; }

.two-columns:nth-of-type(2n) .right {
  background-color: #00415E;
  order: -1; }

/* Content */
.main-content-block {
  width: 100%;
  max-width: 980px;
  padding: 0 30px;
  margin: 0 auto; }

.main-content-logo {
  display: block;
  width: 100%;
  max-width: 300px;
  height: auto;
  margin: 5% auto; }

.main-featured-image {
  height: 400px;
  height: 35vw;
  background: url("https://images.pexels.com/photos/1058277/pexels-photo-1058277.jpeg?auto=compress&cs=tinysrgb&dpr=2&w=3000");
  background-repeat: no-repeat;
  background-position: center -20vw;
  background-size: cover;
  margin: 30px 0; }

/* Footer */
.footer {
  background: url("https://thedarling.com/wp-content/themes/the-darling/img/footer-bg.jpg") repeat center center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 50px 0;
  position: relative; }
  .footer-content {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    max-width: 950px;
    padding: 0 30px;
    margin-bottom: 30px;
    z-index: 10;
    color: #00415E; }
    .footer-content .block {
      width: 33.333%;
      padding: 0 15px; }
      .footer-content .block h4 {
        font-family: 'Eund W00';
        margin-bottom: 10px;
        text-transform: uppercase;
        letter-spacing: 0.1rem; }
      .footer-content .block p {
        font-size: 15px;
        line-height: 24px;
        margin-bottom: 5px;
        text-transform: none; }
  .footer .credits {
    color: #00415E;
    z-index: 2;
    text-transform: none; }

@media only screen and (max-width: 479px) {
  .header-container .address, .header-container .reservation {
    display: none; }
  .more .two-columns {
    flex-direction: column; }
    .more .two-columns .left, .more .two-columns .right {
      width: 100%; }
  .more .two-columns:nth-of-type(2n) .right {
    order: 1; } }

@media only screen and (max-width: 767px) {
  .hero-content {
    padding: 0 30px; }
  .footer-content {
    flex-direction: column;
    align-items: center; }
    .footer-content .block {
      margin-bottom: 20px;
      text-align: center; } }

@media only screen and (max-width: 991px) {
  .header-container .address, .header-container .reservation {
    display: none; } }
